<template>
  <v-container>
    <v-row>
      <v-col justify="center" align="center">
        <v-img src="../assets/logo.png" max-height="200" max-width="250">
        </v-img>
      </v-col>
    </v-row>

    {{ index + 1 }}
    <v-row>
      <v-col>
        <v-card>
          <v-card-text>
            {{ question }}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-card>
          <v-card-text align="" @click="setlog(0)">
            {{ text_1 }}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
        <v-card>
          <v-card-text align="" @click="setlog(1)">
            {{ text_2 }}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
        <v-card>
          <v-card-text align="" @click="setlog(2)">
            {{ text_3 }}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "StartCmp",

  data: () => ({
    textArr: [
      ["톰", "코린", "세인트"],
      ["드래곤본", "티플링", "휴먼"],
      ["파이터", "워록", "위자드"],
      ["군인", "도둑", "귀족"],
      ["춤", "노래", "싸움"],
      ["클라이밍", "베이커리", "포커"],
      ["부자가 되고 싶다", "우주 최강", "뛰어난 모험가"],
    ],
    questionArr: [
      "당신의 이름은 무엇입니까",
      "당신의 종족은 무엇입니까",
      "당신의 클래스는 무엇입니까",
      "당신은 어떤 배경을 가지고 있습니까",
      "당신의 장기는 무엇입니까",
      "당신은 어떤 취미가 있습니까",
      "당신이 미래에 하고 싶은 것이 있나요",
    ],
    log: "",
    index: 0,
    text_1: "",
    text_2: "",
    text_3: "",
    question: "",
  }),
  methods: {
    init() {
      this.log = "";
      this.index = 0;
      this.text_1 = this.textArr[0][0];
      this.text_2 = this.textArr[0][1];
      this.text_3 = this.textArr[0][2];
      this.question = this.questionArr[0];
    },
    setlog(e) {
      this.log += e;
      this.index += 1;
      this.selectTextOnArr();
    },
    selectTextOnArr() {
      //console.log("selectTextOnArr");
      console.log("log : " + this.log);
      console.log("index : " + this.index);

      if (this.textArr.length <= this.index) {
        alert("마지막에 도달하였습니다");
        this.$router.push({
          name: "result",
          params: { log: this.log },
        });
        //this.init();
        //return;
      }

      this.text_1 = this.textArr[this.index][0];
      this.text_2 = this.textArr[this.index][1];
      this.text_3 = this.textArr[this.index][2];
      this.question = this.questionArr[this.index];
    },
  },
  created() {
    this.init();
  },
};
</script>
